@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;1,300&display=swap);
#login-wrapper {
  background-color: rgb(221, 221, 221);
  display: flex;
  height: 100vw;
  justify-content: center;
  width: 100%;
}

#login-form {
  font-size: 1.5em;
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 13em;
  height: 8em;
  justify-content: center;
  margin-top: 3%;
  background-color: white;
  border-radius: 4%;
}
body {
  text-align: center;
}

#header {
  align-items: center;
  background-color: white;
  display: flex;
  justify-content: space-between;
  padding: 0px 10px;
}

#wallet-wrapper {
  width: 100%;
}

#wallet-img {
  width: 55px;
  padding-left: 4px;
}

#user-info {
  display: flex;
  flex-shrink: 0;
  justify-content: flex-end;
  grid-gap: 20px;
  gap: 20px;
  width: 50vw;
}

#expense-form {
  align-items: center;
  background-color: rgb(66, 66, 66);
  color: white;
  display: flex;
  grid-gap: 10px;
  gap: 10px;
  height: 70px;
  width: 100%;
  padding: 0px 10px;
}

.expense-input {
  margin: 0px 5px;
}

table {
  align-items: center;
  border: 1px solid black;
  justify-content: center;
  width: 100%;
  text-align: center; 
  vertical-align: middle;
  padding: 0px 20px
}
body {
  font-family: 'Open Sans';
  font-size: 16px;
  margin: 0;
}

input, button {
  font-size: 16px;
}
